import { Component, Element, h, Host, State } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';
import { TimeDatePresentationUtil } from '../../../../utils/time-date-presentation.util';
import { DateFormat } from '../../../../types';

export interface TitleSubtitleDateData {
    date: string;
}

export interface ITitleSubtitleDateCellParams extends ICellRendererParams {
    getTitleSubtitleDateData?: (data: any) => TitleSubtitleDateData;
}

/**
 * @category Table Cell
 * @description A cell component for displaying a title and subtitle with a date.
 */

@Component({
    tag: 'at-title-subtitle-date-cell',
    shadow: false,
})
export class AtTitleSubtitleDateCell implements ICellRendererComp {
    @Element() el: HTMLElement;
    @State() value: Date;
    @State() params: ICellRendererParams;

    init(params: ICellRendererParams): void {
        this.params = params;
        if (typeof params.value === 'string') {
            this.value = new Date(params.value);
        } else {
            this.value = params.value;
        }
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: ICellRendererParams): boolean {
        this.updateCell(params.value);
        return true;
    }

    private updateCell(newValue: Date) {
        this.params.value = newValue;
        this.value = newValue;
    }

    get hourMinuteSecond(): string {
        return TimeDatePresentationUtil.getFormattedDate(
            this.value,
            DateFormat.HOURS_MINUTES_SECONDS,
        );
    }

    get yearMonthDay(): string {
        return TimeDatePresentationUtil.getFormattedDate(
            this.value,
            DateFormat.YEAR_MONTH_DAY,
        );
    }

    render() {
        return (
            <Host>
                <div class="flex h-full flex-col justify-center">
                    <div class="truncate text-sm leading-[100%]">
                        {this.hourMinuteSecond}
                    </div>
                    <div class="text-med truncate text-xs leading-[100%] font-normal">
                        {this.yearMonthDay}
                    </div>
                </div>
            </Host>
        );
    }
}
